.Navbar{
    width: 100%;
    height: 80px;
    background-color: aquamarine;
    top: 0%;
    margin: 0;
    display: flex;
    flex-direction: row;
    /* box-sizing: border-box; */
    align-items: center;
}
.brandname{
    margin-left: 5%;
    font-weight: 800;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 200%;
}
.navlinks{
    list-style-type: none;
    display:flex;
    margin-left: auto;
    margin-right: 5%;
} 
.navlinks li{
    padding: 0px 20px 0px 20px;
}
a{
    text-decoration: none;
    color: black;
}